import { BriefStepsLegend } from "@rp/react/web/common/composite-components"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Box, TextField, Typography } from "@rp/react/web/mui/core"

type Props = {
  customInstructions: string
  handleCustomInstructionsChange: (newValue: string) => void
}

const CustomInstructions = ({ customInstructions, handleCustomInstructionsChange }: Props) => {
  const { isMobile } = useDeviceInfo()
  return (
    <>
      <BriefStepsLegend sx={{ alignItems: "center", mb: 2 }}>
        <Typography>Refine your response (optional):</Typography>
      </BriefStepsLegend>
      <Box
        bgcolor={"elevation.dp01"}
        p={2}
        borderRadius={1}
        mt={1.5}
        ml={!isMobile ? 7 : undefined}
      >
        <Box
          bgcolor={"elevation.dp08"}
          borderRadius={0.5}
          display={"flex"}
          alignItems={"center"}
          mb={1.5}
          width={"fit-content"}
          py={0.25}
          px={0.5}
        >
          <Typography fontSize={"14px"} fontWeight={"bold"}>
            Custom Instructions
          </Typography>
        </Box>
        <Box mt={1}>
          <TextField
            multiline
            minRows={5}
            placeholder="Specify analysis focus, preferred format, or level of detail..."
            value={customInstructions}
            onChange={event => handleCustomInstructionsChange(event.target.value)}
            onKeyDown={event => {
              if (event.key === "Enter") {
                event.stopPropagation()
              }
            }}
          />
        </Box>
      </Box>
    </>
  )
}

export { CustomInstructions }
